import React from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import App from './App';
import reportWebVitals from './reportWebVitals';
import { Session } from '@ftrack/api';
import { getCredentials, initialize } from "@ftrack/web-widget";

function onWidgetLoad() {
  const credentials = getCredentials();

  const session = new Session(
    credentials.serverUrl,
    credentials.apiUser,
    credentials.apiKey
    // 'https://ftrack-studio-demo.ftrackapp.com',
    // 'demo',
    // 'MTRkZDE1NDItMTVlOC00NTRiLTg0MjktNzhlZDg5YjYyNmYyOjpkYTYxMjEwMS0zMWYwLTQzZjgtOTRkMC1kYmQzNTQ0OWUzMmY'
  );

  session.initializing.then(() => {
    const root = ReactDOM.createRoot(document.getElementById('root'));
    root.render(
      <React.StrictMode>
        <App session={session} />
      </React.StrictMode>
    );
  });
}

function onDomContentLoaded() {
  initialize({
    onWidgetLoad: onWidgetLoad
  })
}

window.addEventListener('DOMContentLoaded', onDomContentLoaded);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
