import logo from './logo.svg';
import './App.css';
import { useEffect, useState } from 'react';

function App(props) {
  let session = props.session;

  const [task, setTask] = useState('empty');

  useEffect(() => {
    session.query('select name from Task order by name limit 1')
    .then(result => {
      setTask(result.data[0].name);
    });
  }, [ session ]);

  return (
    <div className="App">
      <header className="App-header">
        <img src={logo} className="App-logo" alt="logo" />
        <p>
          GH Pages
        </p>
        {task}
      </header>
    </div>
  );
}

export default App;
